import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'
import myOrder from '@/components/myorder'
import myActive from '@/components/active'
import myIndex from '@/components/my'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'HelloWorld',
      component: HelloWorld,
      meta: {
          title: '首页',
          keepAlive: true
      }
    },
    {
      path: '/myorder',
      name: 'myOrder',
      component: myOrder,
      meta: {
          title: '订单',
          keepAlive: true
      }
    },
    {
      path: '/active',
      name: 'myActive',
      component: myActive,
      meta: {
          title: '活动',
          keepAlive: true
      }
    },
    {
      path: '/my',
      name: 'myIndex',
      component: myIndex,
      meta: {
          title: '我的',
          keepAlive: true
      }
    }
  ]
})




