<template>
  <el-tabs v-model="activeName" @tab-click="handleClick" id='el-tabs'>
      <el-tab-pane class='grid-content' :span="12" label="热播列表" @click='onClick' name="first">
        <el-row>
          <el-col v-for='item in msg' :key='item.id' class='grid-content list-item' :span="12">
            <img class="grid-content grid-img" :src="item.img_url" alt="">
            <b class="name-movie">{{item.movieName}}</b>
          </el-col>
        </el-row>
      </el-tab-pane>
      <el-tab-pane class='grid-content' @click='onClick' :span="12" label="免费在线" name="second">
          <carouse></carouse>
      </el-tab-pane>
  </el-tabs>
</template>
<script>
  import carouse from '@/page/carouse'
  export default {
    name:'my-meau',
    components: {
      carouse
    },
    data() {
      return {
        activeName: 'first',
        msg: [],
        button:'buttonStyle'
      };
    },
    created:function(){
      let that = this;
      ///api/movie/in_theaters
      this.$http.get('../../static/json/movieList.json')
      .then(function(res){
          that.msg = res.data;
          console.log(JSON.stringify(that.msg))
      });
    },
    mounted () {
     
    },
    methods: {
      handleClick(tab, event) {
          
      },
      onClick:function(){
        
      }
    }
  };
</script>

<style>
  *{
      margin:0;
      padding:0;
      box-sizing:border-box;
    }
    .list-item{
      height:auto;
      float: left;
      padding: 5px;
    }
    .list-item img{
      display: inline-block;
      height:280px;
      width: 100%;
    }
    .name-movie{
      display: inline-block;
      height: 30px;
      width: 100%;
      line-height: 30px;
      font-size:20px;
      overflow : hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
    .el-tabs__header{
      position: fixed;
      top: 60px;
      left: 0;
      right:0;
      padding: 0 24px;
      z-index: 3000;
      background: #fff;
    }
    .el-tabs{
      padding-top: 110px;
      padding-bottom: 80px;
    }

</style>




